  position: absolute;
}

.header__photo.active {
  opacity: 1;
  z-index: 3;
}

.header__photo-1 {
  z-index: 1;
  background-image: url("photo-slider-1.076de2c3.076de2c3.jpg");
}

.header__photo-2 {
  z-index: 2;
