    border-end-end-radius: 30px;
  }
}

.icon {
  background-size: cover;
  width: 24px;
  height: 24px;
  transition: transform .3s;
}

.icon:hover {
  transform: scale(1.2);
}

.icon--menu {
  background-image: url("Menu.0fcc0e0f.0fcc0e0f.svg");
}

@media (width >= 1024px) {
  .icon--menu {
    display: none;
  }
}

.icon--close {
  background-image: url("icon-close.83c4b107.83c4b107.svg");
}

.icon--item {
  background-image: url("icon-item.058de16e.058de16e.svg");
}

.top-bar {
  grid-column: 1 / -1;
  padding-top: 18px;
}

@media (width >= 1024px) {
  .top-bar {
    padding-block: 48px;
  }
}

.top-bar__content {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.top-bar__link {
  letter-spacing: 3px;
  text-transform: uppercase;
  color: #fff;
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 150%;
  text-decoration: none;
  transition: transform .3s;
  position: relative;
}

.top-bar__link:hover {
  transform: scale(1.2);
}

.top-bar__link:after {
  content: "";
  transform-origin: 0;
  background-color: #2060f6;
  width: 100%;
  height: 1px;
  transition: all .3s;
  display: block;
  position: absolute;
  bottom: -8px;
  transform: scale(0);
}

.top-bar__link:hover:after {
  transform: scale(1);
}

.top-bar__icons {
  align-items: center;
  gap: 32px;
  display: flex;
}

.top-bar__logo {
  transition: transform .3s;
  display: flex;
}

.top-bar__logo:hover {
  transform: scale(1.2);
}

.slider {
  z-index: 5;
  position: absolute;
  bottom: 24px;
  left: 20px;
}

.slider__body {
  flex-direction: column;
  gap: 16px;
  display: flex;
}

.slider__buttons {
  gap: 16px;
  display: flex;
}

.slider__block {
  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  transition: transform .3s;
}

.slider__block:hover {
  transform: scale(1.2);
}

.slider__right {
  justify-content: center;
  display: flex;
  position: relative;
}

.slider__right:before {
  content: ">";
  position: absolute;
  top: 2px;
}

.slider__left {
  justify-content: center;
  display: flex;
  position: relative;
}

.slider__left:before {
  content: "<";
  width: 18px;
  position: absolute;
  top: 2px;
}

.slider__info {
  flex-direction: column;
  gap: 8px;
  display: flex;
}

.slider__intro {
  color: #fff;
  letter-spacing: 3px;
  text-align: center;
