  z-index: 10;
  opacity: 0;
  pointer-events: none;
  background-color: #5b5b5b;
  height: 100vh;
  transition: all .3s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translate(-100%);
}

.aside-menu:target {
  opacity: 1;
  pointer-events: all;
  right: 0;
  transform: translate(0);
}

